@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.loading{
    margin-top: 3em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    
    

}

.loading_msg{
    display: flex;
    flex-direction: row;
    grid-gap: 2em;
    gap: 2em;
    align-items: center;
    font-size: 2em;
    height: 2em;
}
.missions-table{
    display: flex;
    width: 100vw;
    justify-content: end;
    padding-right: 2vw;

}
.missions{
    display: flex;
    width: 76.5%;
    flex-direction: column;
    font-weight: 500;

}

.mission-header{
    display: grid;
    grid-template-columns:  3.6fr 2.6fr 1.5fr 1.5fr 0.525fr;
    grid-gap: 1.2em;
    gap: 1.2em;
    margin-top: 0.9em;   
}

.thin_ruler{
    width: 95%;
    display: grid;
    grid-template-columns:  3.9fr 6.1fr;
    background-color: var(--void);
    height: 0.05em;
    align-content: center;
    margin-top: -0.5em;

}
.fat_ruler{
    height: 0.4em;
    background-color: var(--void);
    border-radius: 1em;
}
.mission{
    display: grid;
    grid-template-columns: 3.6fr 2.6fr 1.5fr 1.3fr 0.525fr;
    grid-gap: 1.2em;
    gap: 1.2em;
    margin-top: 0.5em;    
    align-items: center;
    color: var(--moon-dust);
}

.mission-header-cell{
    width: 100%;
    padding-top: 0.6em;
    padding-bottom: 0.6em;
    padding-left: 1em;
    border-radius: 1em;
    margin: 0.2em;
}
.column-title{
    cursor: pointer;
}

.cell{
    background-color: var(--starlight);
    width: 100%;
    padding-top: 1.6em;
    padding-bottom: 1.6em;
    padding-left: 1em;
    border-radius: 1em;
    margin: 0.1em;
    align-self: stretch;
    justify-items: stretch;
}
.center{
    text-align: center;
}

.load-more{
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    flex-direction: row;
    height: 1em;
    padding-left: 1em;
    margin-top: 1em;
}
.page{
    font-weight: bold;
}
.mission:hover  .cell{
    background-color: var(--martian-soil);
    color: var(--white-dwarf);
    transition: 0.2s;
}


.next-icon{
    height:2em;
    justify-self: center;
    display: none;
}
.mission:hover .next-icon{
    display: grid;
}


.arrow{
    display: none;
    height:1em;
    padding-left: 1em;
}
.visible{
    display: inline-block;
}

.arrow-up{
    transition: 0.5s;
}

.arrow-down{
    transform: rotate(180deg);
    transform-origin: center;
    transition: 0.5s;
}
:root {
    --martian-soil: rgb(209, 75, 57);
    --void: rgb(25, 50, 71);
    --distant-star: rgb(244, 221, 181);
    --white-dwarf: rgb(255, 255, 255);
    --starlight: rgb(253, 242, 221);
    --moon-dust: rgb(96, 96, 96);
    max-width: 100vw;
    overflow-x: hidden;
  }
  /* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
  font-family: sans-serif;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

  body{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: rgb(244, 221, 181);
    background-color: var(--distant-star);
    font-family: 'Roboto', sans-serif;
    font-size: medium;
    color:rgb(25, 50, 71);
    color:var(--void);
    background-image: url(/react-coding-exercise/static/media/takeoff.31a44e1b.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    overflow-x: hidden;
    margin-bottom: 5em;
  }

  .btn-primary{
    display: block;
    background-color: rgb(209, 75, 57);
    background-color: var(--martian-soil);
    color: rgb(255, 255, 255);
    color: var(--white-dwarf);
    text-align: center;
    border-radius: 2em;
    border-color: transparent;
    text-transform: uppercase;
    font-weight: bold;
    padding-left: 2em;
    padding-right: 2em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
  }
.header-wrapper{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding-right: 2vw;
}
  .header{
    width: 76.5%;
    height: 16vh;
    display: grid;
    grid-template-columns: 3.6fr 4.2fr 1.2fr 0.525fr;
    grid-gap: 0.5em;
    gap: 0.5em;
    margin-top: 0.5em;    
    align-items: flex-end;
    justify-content: flex-end;
    align-content: flex-end;
  }

  .search_input{
    background-image: url(/react-coding-exercise/static/media/rocket.a663dc2a.svg);
    background-repeat: no-repeat;
    background-position-x: 0.5em;
    height: 2em;
    border-color: transparent;
    border-radius: 2em;
    background-color: rgb(25, 50, 71);
    background-color: var(--void);
    color: rgb(253, 242, 221);
    color: var(--starlight);
    padding-left: 3em;
  }
  .search_input::-webkit-input-placeholder{
    color: rgb(253, 242, 221);
    color: var(--starlight);
  }
  .search_input:-ms-input-placeholder{
    color: rgb(253, 242, 221);
    color: var(--starlight);
  }
  .search_input::placeholder{
    color: rgb(253, 242, 221);
    color: var(--starlight);
  }
  .logo{
    width: 100%;
  }

